.featuredAd-box {
  position: relative;
  left: 50%;
  transform: translate(-50%);
  width: 90%;
  padding: 2rem;
  background-color: white;
  margin: 2rem;
}

.title {
  padding-bottom: 1rem;
  border-bottom: whitesmoke solid 0.2rem;
  text-transform: uppercase;
  font-size: 1.6rem;
  margin-bottom: 2rem;
}

.card-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
