@import '../../Sass/variables.scss';

.dropdownMenu {
  position: absolute;
  top: 10rem;
  left: 0;
  width: 100%;
  z-index: 1;
  padding: 2rem 6rem;
  display: flex;
  flex-wrap: wrap;
  background-color: white;
  box-shadow: 0 0 10px 1px rgba(128, 128, 128, 0.301);
  z-index: 10;
  transition: ease 5s;
  // display: none;
}
.showdropmenu {
  display: flex;
  position: absolute;
  top: 13rem;
  background-color: white;
}

.dropdownMenu::before {
  content: '';
  position: absolute;
  top: -0.5rem;
  left: 19rem;
  background-color: black;
  width: 1rem;
  height: 1rem;
  display: block;
  transform: rotate(45deg);
  z-index: -50;
}

.list-container {
  width: 15%;
  margin: 1rem;
}

.droptitle {
  font-size: 1.5rem;
  margin-bottom: 1rem;
}

.dropdown-links {
  margin: 0.5rem 0;
  cursor: pointer;
  transition: ease 0.3s;
  font-size: 1.2rem;
  &:hover {
    color: $primary;
  }
}

.img-con {
  width: 15%;
  height: 30rem;
}
.drop-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: right;
}
