@import '../../Sass/variables.scss';

.productCard {
  width: 25rem;
  background-color: white;
  margin: 1rem;
  box-shadow: 0 0 10px 2px rgba(128, 128, 128, 0.223);
  padding: 2rem;

  &__img {
    display: block;
    width: 100%;
    height: 20rem;
    object-fit: contain;
    object-position: center;
  }
  &__price {
    font-size: 1.7rem;
    font-weight: 600;
    color: $primary;
  }

  &__title {
    font-size: 1.4rem;
    font-weight: bolder;
    text-decoration: underline;
  }

  &__category {
    color: grey;
    font-size: 1.2rem;
    margin: 1rem 0;
  }
}
