@import '../../Sass/variables.scss';

header {
  box-shadow: 0 1px 5px 1px rgba(128, 128, 128, 0.333);
  z-index: 5;
}

nav {
  padding: 1.5rem 6rem;
  background-color: whitesmoke;
  align-items: center;
  display: flex;
  justify-content: space-between;
}

.logo-container {
  align-items: center;
}
.logo {
  font-family: 'Lobster Two', cursive;
  font-weight: 700;
  font-size: 3rem;
}
.tag {
  font-size: 1.2rem;
  font-weight: 500;
  color: $primary;
}

.link-buttons-con {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 40%;
}

.link-items {
  display: flex;
  align-items: center;
  margin-left: 2rem;
  transition: ease 0.3s;
  &:hover {
    color: $primary;
    top: -0.5rem;
  }
}

.links {
  font-size: 1.3rem;
  text-decoration: none;
}
.link-icon {
  font-size: 1.4rem;
  margin-right: 0.5rem;
}

.postAdd-btn {
  background-color: $primary;
  color: white;
  padding: 0.5rem 2rem;
  text-transform: uppercase;
  border-radius: 0.3rem;
  cursor: pointer;
}

.search-container {
  display: flex;
  align-items: center;
  width: 100%;
}
