@import '../sass/variables.scss';
.img-container {
  width: 100%;
  height: 30rem;
  //   z-index: 1;
  top: 0em;
  position: relative;
  padding: 2rem;
}
.landing-img {
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 100%;
  object-position: center;
  object-fit: cover;
  z-index: -1;
}

.landing-header {
  position: absolute;
  top: 50%;
  left: 30%;
  transform: translate(-50%, -50%);
  width: fit-content;
}

.landing-h1 {
  font-size: 6rem;
  font-weight: 900;
  font-family: 'Poppins', sans-serif;
}

.slogan {
  font-size: 3rem;
  font-weight: 900;
  font-family: 'Poppins', sans-serif;
  //   background-color: black;
  //   color: $primary;
  margin: 5rem 0;
  padding: 0.5rem 1rem;
}

.h1-highlight {
  color: $primary;
}
