$primary: #ee1e44;
$dark-text-secondary: rgba(255, 255, 255, 0.7);
$dark-text-disabled: rgba(255, 255, 255, 0.5);
$dark-button-action-active: #fff;
$dark-button-action-hover: rgba(255, 255, 255, 0.08);
$dark-button-action-selected: rgba(255, 255, 255, 0.16);
$dark-button-action-disabled: rgba(255, 255, 255, 0.3);
$dark-button-action-disabled-background: rgba(255, 255, 255, 0.12);
$dark-red: black;
$dark-background: #121212;
$dark-divider: rgba(255, 255, 255, 0.12);
$gold: #ae9553;
$background: rgba(255, 255, 255, 0.08);

$desktop-xlarge: 1400px;
$desktop-large: 1200px;
$tablet-landscape: 992px;
$tablet-portrait: 768px;
$mobile-landscape: 576px;
$mobile-portrait: 375px;

@mixin breakpoint($point) {
  @if $point==desktop-xlarge {
    @media (max-width: $desktop-xlarge) {
      @content;
    }
  }
  @if $point==desktop-large {
    @media (max-width: $desktop-large) {
      @content;
    }
  }
  @if $point==tablet-landscape {
    @media (max-width: $tablet-landscape) {
      @content;
    }
  }
  @if $point==tablet-portrait {
    @media (max-width: $tablet-portrait) {
      @content;
    }
  }
  @if $point==mobile-landscape {
    @media (max-width: $mobile-landscape) {
      @content;
    }
  }
  @if $point==mobile-portrait {
    @media (max-width: $mobile-portrait) {
      @content;
    }
  }
}
