.allCatBtn {
  display: flex;
  align-items: center;
  padding: 0.5rem;
  // border: black solid 1px;
  border-radius: 0.3rem;
  color: black;
  cursor: pointer;
  text-transform: uppercase;
  font-weight: 600;
  background-color: transparent;
  transition: ease 0.3s;
  &:hover {
    background-color: whitesmoke;
  }
}

.down-icon {
  margin-left: 0.5rem;
  font-size: 2rem;
  // color: grey;
}
